
.user-navbar {
  background-color: #333;
  border: 4px solid #fff;
  border-radius: 0;
}
.user-navbar .navbar-header .navbar-brand {
    color: #fff;
}
.user-navbar .navbar-toggle {
    border: none;
}
.user-navbar .navbar-toggle:hover .icon-bar {
      background-color: #FFF100;
}
.user-navbar .navbar-toggle:focus {
      background-color: transparent;
}
.user-navbar .navbar-toggle .icon-bar {
      background-color: #fff;
}
.user-navbar .navbar-toggle:hover {
      background-color: transparent;
}
.user-navbar #navbar .nav li.active:hover a {
    color: #000;
}
.user-navbar #navbar .nav li:hover a {
    color: #FFF100;
}
.user-navbar #navbar .nav li a {
    color: #fff;
}



