
.my-card {
  position: relative;
  overflow: hidden;
}
.my-card img {
    transition: all .5s;
}
.my-card .my-card-body {
    width: 100%;
    height: 100%;
    padding: 14px;
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
    opacity: 0;
    transition: all .5s;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-size: 12px;
}
.my-card .my-card-body .my-card-link {
      background-color: #FFF100;
      color: #000;
      padding: .1rem 1rem;
      text-decoration: none;
      display: inline-block;
      margin-top: 2px;
}
.my-card:hover .my-card-body {
    opacity: 1;
}



